.tariffs {
  padding: 15px 0 100px;
  &__title {
    position: relative;
    z-index: 2;
    font-size: 24px;
    line-height: 100%;
    font-weight: 700;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 100%;
    color: #9F9F9F;
    margin-top: 15px;
  }
  &-payment {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    background: #fff;
    box-shadow: 0px 0px 91.9px 0px rgb(232, 232, 232);
    border-radius: 12px;
    padding: 30px;
    margin-top: 50px;
    &__title {
      font-size: 24px;
      line-height: 100%;
      font-weight: 700;
    }
    &__items {
      display: flex;
      align-items: stretch;
      gap: 20px;
    }
    &__item {
      position: relative;
    }
    &__radio {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    &__radio:checked ~ .tariffs-payment__group {
      border-color: #5856D2;
      .tariffs-payment__text {
        color: #5856D2;
      }
    }
    &__group {
      min-width: 332px;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      gap: 20px;
      background: #F8F8FC;
      border: 3px solid transparent;
      border-radius: 10px;
      padding: 15px 20px;
    }
    &__icon {
      max-width: 115px;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
    &__text {
      font-size: 20px;
      line-height: 100%;
      font-weight: 600;
    }
  }
  &__wrap {
    margin-top: 50px;
  }
  &__radio-btns {
    display: flex;
    align-items: stretch;
    gap: 15px;
    margin-top: 25px;
  }
  &__radio {
    position: relative;
    input {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      background: #5856D2;
      color: #fff;
    }
    label {
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      box-shadow: 0px 0px 91.9px 0px rgb(232, 232, 232);
      border-radius: 12px;
      padding: 5px 32px;
      font-size: 16px;
      line-height: 125%;
      font-weight: 500;
    }
  }
  &__tabs {
    margin-top: 20px;
  }
  &__tabs-head {
    display: none;
    margin: 0 !important;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    align-items: start;
    gap: 30px;
  }
}
.tariff-card {
  text-align: center;
  background: #fff;
  box-shadow: 0px 0px 91.9px 0px rgb(232, 232, 232);
  border-radius: 12px;
  padding-bottom: 10px;
  &__title {
    padding: 20px;
    font-size: 24px;
    line-height: 100%;
    font-weight: 600;
    text-transform: uppercase;
  }
  &__price {
    width: calc(100% - 6px);
    background: #F8F8FC;
    border-radius: 12px;
    padding: 20px;
    font-size: 24px;
    line-height: 100%;
    font-weight: 600;
    color: #5856D2;
    margin: 0 auto;
    span {
      font-size: 32px;
    }
  }
  &__list {
    padding: 0 20px;
    li {
      position: relative;
      max-width: 336px;
      padding: 20px 0;
      font-size: 16px;
      line-height: 100%;
      font-weight: 500;
      margin: 0 auto;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background: #EBEBF3;
        pointer-events: none;
      }
    }
    li:last-child {
      &:after {
        display: none;
      }
    }
  }
  &__btn {
    width: calc(100% - 70px);
    height: 50px;
    padding: 5px 15px;
    margin: 10px auto 20px;
    span {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .tariffs {
    &-payment {
      &__items {
        flex-shrink: 0;
      }
      &__group {
        min-width: unset;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .tariffs {
    &__subtitle {
      max-width: 480px;
    }
    &-payment {
      flex-direction: column;
      align-items: start;
      gap: 20px;
      margin-top: 20px;
      &__title {
        font-size: 20px;
      }
      &__items {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, calc(50% - 20px / 2));
      }
      &__group {
        border-width: 2px;
      }
      &__icon {
        max-width: 60px;
      }
      &__text {
        font-size: 16px;
      }
    }
    &__radio {
      flex-grow: 1;
      input:checked ~ label {
        position: relative;
        z-index: 2;
        box-shadow: unset;
      }
    }
    &__tabs {
      margin-top: 15px;
    }
    &__tabs-head {
      display: flex;
    }
    &__cards {
      grid-template-columns: 100%;
      margin-top: 30px;
    }
    .tab__content2 {
      display: none;
    }
    .tab__content2--active {
      display: block;
    }
  }
  .tariff-card {
    &__list {
      li {
        max-width: 100%;
        &:after {
          left: 50%;
          transform: translate(-50%, 0);
          max-width: 525px;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .tariffs {
    padding: 15px 0 50px;
    &__title {
      font-size: 20px;
    }
    &__subtitle {
      max-width: 270px;
      font-size: 14px;
      margin-top: 10px;
    }
    &-payment {
      gap: 15px;
      padding: 20px;
      &__title {
        font-size: 16px;
      }
      &__items {
        grid-template-columns: 100%;
        gap: 10px;
      }
      &__text {
        font-size: 14px;
      }
    }
    &__radio-btns {
      gap: 10px;
      margin-top: 15px;
    }
    &__radio {
      label {
        height: 40px;
        padding: 5px 15px;
        font-size: 14px;
      }
    }
    &__tabs {
      margin-top: 10px;
    }
    &__cards {
      margin-top: 20px;
    }
  }
  .tariff-card {
    padding-bottom: 5px;
    &__title {
      padding: 15px;
      font-size: 18px;
    }
    &__price {
      padding: 15px;
      font-size: 18px;
      span {
        font-size: 24px;
      }
    }
    &__list {
      padding: 0 15px;
      li {
        padding: 15px 0;
        font-size: 14px;
        &:after {
          max-width: 250px;
        }
      }
    }
    &__btn {
      width: calc(100% - 30px);
      height: 50px;
    }
  }
}

@media screen and (max-width: 374px) {
  .tariffs {
    &__radio-btns {
      display: grid;
      grid-template-columns: 100%;
    }
  }
}