.products-add {
  padding: 15px 0 100px;
  .basket__group:first-child {
    margin-top: 0;
  }
  &__flex {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px / 2));
    gap: 20px;
    margin: 20px 0 60px;
  }
  &__desc {
    padding-top: 10px;
    nav:not(:last-child) {
      margin-bottom: 20px;
    }
    h5 {
      font-size: 20px;
      line-height: 100%;
      font-weight: 700;
      margin-bottom: 10px;
    }
    ol {
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
      li {
        list-style: auto;
        list-style-position: inside;
        font-size: 14px;
        line-height: 100%;
        color: #9F9F9F;
      }
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
      li {
        font-size: 14px;
        line-height: 100%;
        color: #9F9F9F;
      }
    }
  }
  .basket__group {
    display: block;
  }
  .form-group {
    textarea {
      max-width: 820px;
    }
  }
  .file-input {
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .form-check {
    margin-top: 0;
  }
  .select2-multiple {
    width: 100%;
  }
  .select2 {
    max-width: 477px;
    &-selection {
      display: block !important;
    }
    &-search {
      display: inline-flex;
      align-items: center;
    }
  }
  &__btn {
    height: 55px;
    padding: 5px 45px;
    margin-top: 30px;
    span {
      font-size: 16px;
    }
  }
  .form-delivery {
    width: 100%;
    &__group {
      max-width: 545px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      gap: 10px 20px;
      background: #EDEDFA;
      border-radius: 12px;
      padding: 25px 20px;
    }
    .form-check {
      label {
        font-size: 16px;
        &:after {
          top: 50%;
          transform: translate(0, -50%);
          background-color: #fff;
        }
      }
    }
    &__btns {
      display: flex;
      align-items: center;
      gap: 20px;
    }
    &__change {
      background: transparent;
      padding: 0;
      &:hover {
        span {
          color: #5856D2;
        }
      }
      span {
        font-size: 16px;
        line-height: 100%;
        color: #9696B7;
        transition: all 0.3s;
      }
    }
    &__delete {
      width: 13px;
      background: transparent;
      padding: 0;
      &:hover {
        svg path {
          fill: #5856D2;
        }
      }
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    &__add {
      gap: 5px;
      background: transparent;
      padding: 0;
      margin-top: 12px;
      i {
        width: 24px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 14px;
        line-height: 100%;
        font-weight: 500;
        color: #5856D2;
      }
    }
  }
  .gallery-add {
    &__small {
      margin-top: 10px;
      .swiper-slide {
        position: relative;
        background: #EDEDFA;
        border-radius: 12px;
        padding-bottom: 13.2%;
      }
    }
    .disabled-slide {
      opacity: 0.37;
      pointer-events: none;
    }
    &__img {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EDEDFA;
      border-radius: 12px;
      padding-bottom: 66%;
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        max-width: 147px;
        height: auto;
      }
    }
    &__add {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background: transparent;
      padding: 5px;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        cursor: pointer;
      }
      label {
        svg {
          width: 25px;
          max-width: 80%;
          height: auto;
        }
      }
    }
    &__content {
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      pointer-events: none;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.62);
        border-radius: 12px;
        opacity: 0;
        pointer-events: none;
      }
      &:hover {
        &:after {
          opacity: 1;
        }
        .gallery-add__btns {
          opacity: 1;
          pointer-events: auto;
        }
      }
    }
    &__content--active {
      opacity: 1;
      pointer-events: auto;
    }
    &__img2 {
      width: 100%;
      height: 100%;
      background-color: #EDEDFA;
      border-radius: 12px;
      img {
        width: 100%;
        height: 100%;
        border-radius: 12px;
      }
    }
    &__img3 {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #EDEDFA;
      border-radius: 12px;
      opacity: 0;
      pointer-events: none;
      img {
        width: 100%;
        height: 100%;
        border-radius: 12px;
      }
    }
    &__img3--active {
      opacity: 1;
      pointer-events: auto;
    }
    &__btns {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      align-items: start;
      gap: 10px;
      opacity: 0;
      pointer-events: none;
    }
    &__delete {
      width: 16px;
      background: transparent;
      padding: 0;
      &:hover {
        svg path {
          fill: #5856D2;
        }
      }
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    &__crop {
      width: 21px;
      display: none;
      background: transparent;
      padding: 0;
      &:hover {
        svg path {
          fill: #5856D2;
        }
      }
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
  }
}

@media  screen and (max-width: 992px) {
  .products-add {
    &__flex {
      grid-template-columns: 100%;
      margin-bottom: 40px;
    }
    &__desc {
      padding-top: 0;
    }
  }
}

@media screen and (max-width: 767px) {
  .products-add {
    .form-delivery {
      &__group {
        max-width: 100%;
      }
    }
  }
}

@media  screen and (max-width: 576px) {
  .products-add {
    padding-bottom: 50px;
    &__desc {
      h5 {
        font-size: 18px;
      }
    }
    .file-input {
      padding-top: 10px;
      padding-bottom: 10px;
    }
    .form-delivery {
      .form-check {
        label {
          font-size: 12px;
        }
      }
      &__change {
        span {
          font-size: 14px;
        }
      }
    }
    .gallery-add {
      &__img {
        img {
          max-width: 80px;
        }
      }
      &__add {
        svg {
          width: 16px;
        }
      }
    }
    .gallery-add {
      &__btns {
        gap: 5px;
      }
      &__delete {
        width: 8px;
      }
      &__crop {
        width: 10px;
      }
    }
  }
}