.chat {
  padding: 30px 0 100px;
  &__inner {
    background: transparent;
    border-radius: 0;
    padding: 0 !important;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    .form-check {
      margin-top: 0;
    }
  }
  &__subgroup {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  &__title {
    font-size: 24px;
  }
  &__cards {
    gap: 15px;
  }
  .ellipsis {
    position: relative;
    &:hover {
      .ellipsis-hidden {
        transform: translateY(0);
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
  .ellipsis-show {
    position: relative;
    z-index: 3;
    width: 24px;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  .ellipsis-hidden {
    position: absolute;
    z-index: 2;
    top: calc(100% + 20px);
    right: -20px;
    transform: translateY(-10px);
    width: 265px;
    background: #fff;
    box-shadow: 0px 0px 160.7px 0px rgba(139, 139, 139, 0.25);
    border-radius: 12px;
    padding: 20px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.125s;
    &:after {
      content: '';
      position: absolute;
      top: -32px;
      right: 10px;
      border: 20px solid transparent;
      border-bottom: 20px solid #fff;
    }
    &:before {
      content: '';
      position: absolute;
      top: -24px;
      right: 0;
      width: 100px;
      height: 25px;
      background: transparent;
      opacity: 0;
    }
  }
  .ellipsis-item {
    display: flex;
    align-items: center;
    gap: 8px;
    border-bottom: 1px solid #ECECEC;
    padding: 10px 0;
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      border-bottom: unset;
      padding-bottom: 0;
    }
    &:hover {
      opacity: 0.7;
    }
    i {
      width: 20px;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
    span {
      font-size: 14px;
      line-height: 100%;
      font-weight: 500;
    }
  }
  .message {
    max-width: 365px;
    display: flex;
    align-items: center;
    gap: 10px;
    &--row {
      margin-left: 60px;
    }
    &-img {
      width: 50px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-group {
      display: flex;
      align-items: center;
      gap: 12px;
    }
    &-text {
      font-size: 16px;
      line-height: 100%;
      font-weight: 500;
    }
    &-time {
      flex-shrink: 0;
      font-size: 14px;
      line-height: 100%;
      font-weight: 500;
      color: #A2A7B2;
    }
  }
  .messagemy {
    max-width: 365px;
    display: inline-flex;
    align-items: center;
    gap: 8px;
    background: #EDF1F7;
    border-radius: 8px;
    padding: 12px 16px;
    margin-left: auto;
    &-text {
      font-size: 16px;
      line-height: 100%;
      font-weight: 600;
    }
    &-group {
      flex-shrink: 0;
      display: flex;
      align-items: center;
      gap: 5px;
    }
    &-time {
      font-size: 14px;
      line-height: 100%;
      font-weight: 500;
      color: #9F9F9F;
    }
    .read {
      width: 22px;
      img {
        width: 100%;
      }
    }
  }
}
.chat-card {
  align-items: stretch;
  border-radius: 10px;
  border: unset !important;
  padding: 15px 20px !important;
  &:hover {
    background: #EDEDFF;
    .chat-card__check {
      label {
        &:after {
          background-color: #fff;
        }
      }
    }
    .ellipsis {
      display: block;
    }
    .chat-card__delete {
      display: block;
    }
  }
  .user-img {
    position: absolute;
    z-index: 2;
    top: -10px;
    right: -10px;
    width: 47px;
  }
  &__right {
    max-width: 100% !important;
    flex-grow: 1;
    align-items: stretch;
    gap: 30px;
  }
  &__group {
    max-width: 815px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__group2 {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 100%;
    font-weight: 500;
    margin-top: 10px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__text {
    margin-top: 10px;
    p {
      font-size: 16px;
      line-height: 100%;
      color: #9F9F9F;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:2;
      -webkit-box-orient: vertical;
    }
  }
  &__subgroup {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  .read {
    width: 22px;
    img {
      width: 100%;
    }
  }
  .notread {
    position: relative;
    margin-right: 20px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: -20px;
      transform: translate(0, -50%);
      width: 10px;
      height: 10px;
      background: #EA5252;
      border-radius: 50%;
    }
  }
  .message-date,
  .message-time {
    font-size: 16px;
    line-height: 100%;
    color: #9F9F9F;
  }
  &__delete {
    width: auto;
    height: auto;
    display: none;
    background: transparent !important;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
      path {
        fill: #C9C9DF !important;
      }
    }
  }
  .ellipsis {
    display: none;
  }
}
.notread-card {
  .chat-card__title,
  .chat-card__subtitle,
  .chat-card__text p,
  .message-date,
  .message-time {
    font-weight: 700;
    color: #3F3F3F;
  }
}
.chat2 {
  &__inner {
    background: #fff;
    border-radius: 20px;
  }
  &__top {
    background: #5856D2;
    border-radius: 20px 20px 0 0;
    padding: 18px 30px;
  }
  &__btn {
    display: flex;
    align-items: center;
    gap: 10px;
    background: transparent;
    padding: 0;
    i {
      width: 23px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 20px;
      line-height: 100%;
      font-weight: 600;
      color: #fff;
    }
  }
}
.chat-user {
  position: relative;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 15px 60px 15px 30px;
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: calc(100% - 60px);
    height: 1px;
    background: #ECECEC;
  }
  &__img {
    position: relative;
    flex-shrink: 0;
    width: 64px;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 2;
    top: -5px;
    right: -5px;
    width: 26px;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 5px;
  }
  &__name {
    font-size: 16px;
    line-height: 100%;
    font-weight: 600;
  }
  &__time {
    font-size: 14px;
    line-height: 100%;
    color: #9F9F9F;
  }
  &__text {
    margin-top: 5px;
    p {
      font-size: 14px;
      line-height: 100%;
      font-weight: 500;
    }
  }
  .ellipsis {
    position: absolute;
    top: 50%;
    right: 30px;
    transform: translate(0, -50%);
  }
}
.messages {
  height: 330px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding-right: 10px;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 3px;
  }
  &::-webkit-scrollbar-track {
    background: #fff;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #5856D2;
    border-radius: 3px;
  }
  &-wrap {
    padding: 30px;
  }
}


@media screen and (max-width: 1200px) {
  .chat-card {
    &__subtitle {
      font-size: 14px;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    .message-date,
    .message-time {
      font-size: 14px;
    }
  }
  .chat2 {
    &__btn {
      i {
        width: 18px;
      }
      span {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .chat-card {
    .ellipsis {
      display: block;
    }
    &__delete {
      display: block;
    }
  }
}

@media screen and (max-width: 767px) {
  .chat-card {
    .user-img {
      width: 32px;
    }
  }
}

@media screen and (max-width: 576px) {
  .chat {
    padding: 15px 0 50px;
    &__top {
      flex-direction: column;
      align-items: start;
    }
    .ellipsis-show {
      width: 16px;
    }
    .ellipsis-hidden {
      top: calc(100% + 10px);
      width: 200px;
      padding: 15px;
      &:after {
        right: 8px;
      }
      &:before {
        top: -11px;
        height: 12px;
        width: 60px;
      }
    }
    .ellipsis-item {
      padding: 8px 0;
      i {
        width: 12px;
      }
      span {
        font-size: 11px;
      }
    }
    .message {
      max-width: 300px;
      &-img {
        width: 30px;
      }
      &-text {
        font-size: 12px;
      }
      &-time {
        font-size: 12px;
      }
    }
    .messagemy {
      max-width: 300px;
      &-text {
        font-size: 12px;
      }
      &-time {
        font-size: 12px;
      }
      .read {
        width: 16px;
      }
    }
  }
  .chat-card {
    flex-direction: row;
    align-items: start;
    padding: 15px !important;
    .user-img {
      top: -5px;
      right: -5px;
    }
    &__right {
      flex-direction: column;
      align-items: start;
      gap: 10px !important;
      padding: 0;
    }
    &__group2 {
      width: 100%;
      order: -1;
      flex-direction: row;
      align-items: center;
    }
    &__subtitle {
      font-size: 12px;
    }
    &__text {
      p {
        font-size: 12px;
      }
    }
    &__subgroup {
      &:nth-child(2) {
        order: -1;
      }
    }
    .read {
      width: 16px;
    }
    .message-date,
    .message-time {
      font-size: 12px;
    }
  }
  .chat2 {
    &__inner {
      border-radius: 15px;
    }
    &__top {
      border-radius: 15px 15px 0 0;
      padding: 18px;
    }
    &__btn {
      span {
        font-size: 16px;
      }
    }
  }
  .chat-user {
    padding: 12px 40px 12px 18px;
    &:after {
      width: calc(100% - 36px);
    }
    &__group {
      flex-direction: column;
      align-items: start;
    }
    &__name {
      font-size: 14px;
    }
    &__time {
      font-size: 12px;
    }
    &__text {
      p {
        font-size: 12px;
      }
    }
    .ellipsis {
      right: 18px;
    }
  }
  .messages {
    height: 300px;
    gap: 24px;
    &-wrap {
      padding: 18px;
    }
  }
}