.auctions {
  padding: 15px 0 100px;
  .catalog__selects {
    margin-top: 20px;
  }
  .sorting-select {
    .nice-select {
      min-width: 275px;
      padding-left: 36px;
      &:before {
        content: "";
        position: absolute;
        top: 50%;
        left: 15px;
        transform: translate(0, -50%);
        width: 15px;
        height: 15px;
        background-image: url(../img/sorting-icon.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
      }
    }
  }
  .nice-select {
    justify-content: space-between;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
    margin-top: 20px;
  }
  .work-card {
    &::after {
      display: none;
    }
    &__desc {
      padding: 0;
      margin-top: 10px;
    }
    .sale {
      padding: 7px 19px;
    }
    &__title {
      min-height: 62px;
      font-size: 22px;
    }
    &__btn2 {
      height: 50px;
      border-radius: 12px;
      padding: 5px 40px;
      &:hover {
        background: #5856d2;
        color: #fff;
      }
      span {
        font-size: 15px;
      }
    }
  }
  .pagination {
    margin-top: 30px;
  }
  
}
.contests2 {
  .auctions__cards {
    gap: 40px 20px;
  }
}

@media screen and (max-width: 1200px) {
  .auctions {
    .work-card {
      &__title {
        min-height: 45px;
        font-size: 17px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .auctions {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 32px / 3));
      gap: 16px;
    }
  }
  .contests2 {
    .auctions__cards {
      gap: 32px 16px;
    }
}
}

@media screen and (max-width: 767px) {
  .auctions {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
    }
    .work-card:nth-child(12)~.work-card {
      display: none !important;
    }
  }
}

@media screen and (max-width: 576px) {
  .auctions {
    padding: 15px 0 50px;
    .catalog__selects {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 5px));
      gap: 10px;
    }
    .nice-select {
      width: 100%;
      min-width: unset !important;
    }
    .work-card {
      &__title {
        min-height: 37px;
        font-size: 14px;
      }
      &__btn2 {
        height: 38px;
        padding: 5px 10px;
        span {
          font-size: 12px;
        }
      }
    }
    .pagination {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 359px) {
  .auctions {
    .catalog__selects {
      grid-template-columns: 100%;
    }
    &__cards {
      grid-template-columns: 100%;
    }
  }
}